import React, { Component } from 'react';
import GoogleMapReact from 'google-map-react';
import { AiFillFire } from 'react-icons/ai'
import fire from '../../assests/images/fire.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import * as FontIcons from '@fortawesome/free-solid-svg-icons';
import { MultiSelect } from 'primereact/multiselect';


const AnyReactComponent = ({ text }) => <div style={{ width: '30% !important' }} >{text}</div>;

export default class GoogleMap extends Component {

  arrayOfEvent = [
    { label: 'Drought', value: 'Drought' },
    { label: 'Dust and Haze', value: 'Dust and Haze' },
    { label: 'Earthquakes', value: 'Earthquakes' },
    { label: 'Floods', value: 'Floods' },
    { label: 'Landslides', value: 'Landslides' },
    { label: 'Sea and Lake Ice', value: 'Sea and Lake Ice' },
    { label: 'Severe Storms', value: 'Severe Storms' },
    { label: 'Snow', value: 'Snow' },
    { label: 'Temperature Extremes', value: 'Temperature Extremes' },
    { label: 'Volcanoes', value: 'Volcanoes' },
    { label: 'Water Color', value: 'Water Color' },
    { label: 'Wildfires', value: 'Wildfires' }
  ];
  styleEvent = {
    "Drought": { icon: FontIcons.faTintSlash, color: 'blue' },
    "Dust and Haze": { icon: FontIcons.faSmog, color: '#efa51e' },
    "Earthquakes": { icon: FontIcons.faGlobe, color: 'green' },
    "Floods": { icon: FontIcons.faWater, color: '#0000d8' },
    "Landslides": { icon: FontIcons.faMountain, color: '#00bb00' },
    "Sea and Lake Ice": { icon: FontIcons.faSnowflake, color: '#f13af1' },
    "Snow": { icon: FontIcons.faSnowman, color: 'grey', title: "#33a3b5" },
    "Severe Storms": { icon: FontIcons.faPooStorm, color: '#dea131' },
    "Temperature Extremes": { icon: FontIcons.faTemperatureHigh, color: 'red' },
    "Volcanoes": { icon: FontIcons.faStarOfLife, color: '#cf1020' },
    "Wildfires": { icon: FontIcons.faFireAlt, color: '#e25822' },
    "Water Color": { icon: FontIcons.faHandHoldingWater, color: '#fb5d92' },
  }

  static defaultProps = {
    center: {
      lat: 0,
      lng: 0,
    },
    zoom: 0
  };

  constructor(props) {
    super(props);
    this.state = {
      naturalEventData: [],
      info: null,
      selectedEvents: [],
    }
    for (var i = 0; i < this.arrayOfEvent.length; i++) {
      this.state.selectedEvents.push(this.arrayOfEvent[i].label);
    }
  }

  eventsTemplate = (option) => {
    return (
      <div className="country-item">
        <FontAwesomeIcon icon={this.styleEvent[option.label].icon} style={{ color: this.styleEvent[option.label].color }} />
        <span>{option.label}</span>
      </div>
    );
  }

  onChangeEvent(event) {
    this.setState({ selectedEvents: event.value });
  }

  async componentWillReceiveProps(props) {
    await this.setState({ naturalEventData: props.naturalEventData });
    console.log("Inside Google Map");
    console.log(this.state.naturalEventData);
  }

  locationInfo(infoObj) {
    this.setState({ info: infoObj });
  }

  render() {

    var naturalEventData = this.state.naturalEventData;
    var locMarker = [];
    for (var k = 0; k < naturalEventData.length; k++) {
      var rec = naturalEventData[k];
      if (this.styleEvent[rec.categories[0].title] != null && rec.geometries[0].type === "Point"  && this.state.selectedEvents.includes(rec.categories[0].title)) {
        for (let i = 0; i < rec.geometries.length; i++) {
          if(rec.categories[0].title === "Volcanoes" && (rec.geometries[i].coordinates[1] == undefined || rec.geometries[i].coordinates[0] == undefined) ){
            console.log(rec.id) ;
            console.log(rec.geometries[i].coordinates[0]) ;
          }
          let point = <AnyReactComponent key={k+"_"+i}
            lat={rec.geometries[i].coordinates[1]}
            lng={rec.geometries[i].coordinates[0]}
            text={<FontAwesomeIcon icon={this.styleEvent[rec.categories[0].title].icon} style={{ color: this.styleEvent[rec.categories[0].title].color }} />}
          />
          locMarker.push(point);
        }
      }
    }
    return (
      <div className="mapClass mb-4" style={{width: '100%' }}>
        <div className="titlePage p-2" >The Earth Observatory Natural Event Tracker (EONET)</div>
        <div><small className="text-muted">Users can browse the entire globe daily and look for natural events as they occur. Storms are regularly spotted in the tropics, dust storms over deserts, forest fires in the summers.</small></div>
        <div className="multiselect p-2">
          <label className="text-white mr-2"><h5>Select Event :</h5></label>
          <MultiSelect value={this.state.selectedEvents} options={this.arrayOfEvent}
            onChange={(e) => this.onChangeEvent(e)} filter={true}
            itemTemplate={this.eventsTemplate} placeholder="Select Events"
            maxSelectedLabels={1} 
          />
        </div>
        <div className="googleMap " >
          <GoogleMapReact
            bootstrapURLKeys={{ key: "AIzaSyAq6157Lup2pN7zW4ukwvAkxu-PjTd8udA" }}
            defaultCenter={this.props.center}
            defa
            defaultZoom={this.props.zoom}
          >
            {locMarker}
          </GoogleMapReact>
        </div>

      </div>
    );
  }
}
